<template>
    <div class="signMessage">
        <div class="coverImg">
            <h4></h4>
            <el-row>
                <el-col :xs="24" :sm="16" :md="14" :lg="10" :xl="8">
                    <el-upload
                        class="upload-demo"
                        action=""
                        :on-change="addFile"
                        :auto-upload=false
                        :multiple=false
                        :show-file-list=false
                        drag>
                        <img v-if="imageUrl" :src="imageUrl" class="cover">
                        <div v-else>
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text"><em></em></div>
                        </div>
                    </el-upload>
                </el-col>
                <el-col :xs="24" :span="9">
                    <p> 800px*600pxjpgpnggif3MB</p>
                </el-col>
            </el-row>
            <div class="small-font tips"> Logo </div>
        </div>
        <div class="title">
            <h4></h4>
            <div class="input">
                <el-input type="text" maxlength="26" v-model="title" placeholder="26"></el-input>
                <span class="num-limit">{{ title.length }}/26</span>
            </div>
            <div class="small-font tips"></div>
        </div>
        <div class="short-title">
            <h4></h4>
            <div class="input">
                <el-input type="text" maxlength="10" v-model="shortTitle" placeholder="10"></el-input>
                <span class="num-limit">{{ shortTitle.length }}/10</span>
            </div>
            <div class="small-font tips"></div>
        </div>
        <div class="intro">
            <h4></h4>
            <div class="textarea">
                <el-input type="textarea" v-model="intro" :maxlength="120" :rows="5" resize="none" placeholder="120"></el-input>
                <span class="num-limit">{{ intro.length }}/120</span>
            </div>
            <div class="small-font tips"></div>
        </div>
        <div class="class">
            <h4></h4>
            <el-autocomplete
                popper-class="my-autocomplete"
                v-model="category"
                :fetch-suggestions="querySearch"
                placeholder=""
                @select="handleSelect">
                <i
                    class="el-icon-edit el-input__icon"
                    slot="suffix">
                </i>
                <template slot-scope="{ item }">
                    <el-tag
                        :type="getColorType(4)">
                        {{ item }}
                    </el-tag>
                </template>
            </el-autocomplete>
        </div>
        <div class="address">
            <h4></h4>
            <el-row>
                <el-col :xs="10" :sm="8" :span="6">
                    <el-dropdown split-button @command="getProvince">
                        {{ province }}
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item v-for="(item,i) in provinces" :key="i" :command="item">{{ item }}</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </el-col>
                <el-col :xs="11"  :span="6" :offset="1">
                    <el-dropdown split-button  @command="getCity">
                        {{ city }}
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item v-for="(item,i) in cities" :key="i" :command="item">{{ item }}</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </el-col>
            </el-row>
        </div>
        <div class="interval">
            <h4></h4>
            <div class="block">
                <span class="demonstration"></span>
                <el-date-picker
                v-model="interval"
                @change="getInterval"
                type="datetimerange"
                range-separator=""
                start-placeholder=""
                end-placeholder="">
                </el-date-picker>
            </div>
            <div class="small-font tips">6030-45</div>
        </div>
        <div class="targetMoney">
            <h4></h4>
            <div>
                <el-input v-model="money" placeholder="2"></el-input>
            </div>
            <div class="small-font tips"></div>
        </div>
         <div class="nextStep">
             <el-alert
                title=""
                type="warning"
                show-icon>
            </el-alert>
            <el-button class="preBtn" @click="nextStep(-1)"></el-button>
            <el-button class="nextBtn" type="danger" @click="nextStep(1)"></el-button>
         </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            imageUrl: sessionStorage.getItem('imageUrl') || '',
            title:sessionStorage.getItem('title') || '',
            shortTitle:sessionStorage.getItem('shortTitle') || '',
            intro:sessionStorage.getItem('intro') || '',
            category:sessionStorage.getItem('category') || '',
            categorys: ['','','','','','','','','','','','',''],
            colorType:['','success','info','warning','danger'],
            provinces:['','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','','',''],
            province:sessionStorage.getItem('province') || '',
            cities:[],
            city:sessionStorage.getItem('city') || '',
            interval:[new Date(parseInt(sessionStorage.interval_0)),new Date(parseInt(sessionStorage.interval_1))],
            money:sessionStorage.money || '',
        };
    },
    methods: {
        nextStep(data){
            if(data === 1){
                if(this.imageUrl && this.title && this.shortTitle && this.intro 
                && this.category && this.province && this.city 
                && this.money){
                    if(this.categorys.indexOf(this.category) == -1){
                        this.$message.error('')
                        return false
                    }else if(isNaN(this.interval[0] - this.interval[1])){
                        this.$message({
                            type:'warning',
                            message:''
                        })
                        return
                    }else if(this.interval[0] - this.interval[1] == 0){
                        this.$message({
                            type:'warning',
                            message:''
                        })
                        return
                    }
                    else if(Math.floor((this.interval[1] - this.interval[0])/86400000) > 60){
                        this.$message.error('60')
                        return false
                    }else if(isNaN(Number(this.money)) || Number(this.money) <= 0){
                        this.$message({
                            type:'warning',
                            message:''
                        })
                        return
                    }
                }else{
                    this.$message({
                        type:'warning',
                        message:','
                    })
                    return
                }
            }
            this.saveData()
            this.$emit('nextStep',data)
        },
        addFile(file) {
            console.log(file)
            const isLt3M = file.size / 1024 / 1024 < 3

            if(['image/png', 'image/gif', 'image/jpg','image/jpeg'].indexOf(file.raw.type) == -1){
                this.$message.error('')
                return false
            }
            if(!isLt3M){
                this.$message.error('3MB')
                return false
            }
            this.imageUrl = file.url
        },
        saveData(){
            sessionStorage.setItem('imageUrl',this.imageUrl)
            sessionStorage.setItem('title',this.title)
            sessionStorage.setItem('shortTitle',this.shortTitle)
            sessionStorage.setItem('intro',this.intro)
            sessionStorage.setItem('category',this.category)
            sessionStorage.setItem('province',this.province)
            sessionStorage.setItem('city',this.city)
            this.saveInterval(this.interval)
            sessionStorage.money = this.money
        },
        querySearch(queryString, cb) {
            //  callback 
            cb(this.categorys);
        },
        createFilter(queryString) {
            return (categorys) => {
                return (categorys.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            }
        },
        getColorType(n){
            return this.colorType[parseInt(Math.random()*n)]
        },
        handleSelect(item) {
            this.category = item
        },
        getProvince(command){
            this.province = command
            this.$axios.post('/getCity',{
                province:command
            }).then(res => {
                this.cities = res.data
            }).catch(err => {
                this.$message.error('')
            })
        },
        getCity(command){
            this.city = command
        },
        getInterval(val){
            this.interval = val
        },
        saveInterval(arr){
            let one = JSON.stringify(arr[0].getTime())
            let sec = JSON.stringify(arr[1].getTime())
            sessionStorage.setItem('interval_0',one)
            sessionStorage.setItem('interval_1',sec)
        },
    }
}
</script>
<style lang="scss" scoped>
.signMessage{
    margin: 50px 10px;
    position: relative;
    h4{
        color:black;
    }
    .small-font{
        font-size: 14px;
    }
    .tips{
        margin:10px 0;
        padding: 10px 5px;
    }
    .input{
        position:relative;
        .num-limit{
            position: absolute;
            right:5px;
            top: 10px;
        }
    }
    .textarea{
        position:relative;
        .num-limit{
            position: absolute;
            right:5px;
            top: 90px;
        }
    }
    .cover{
        width: 100%;
    }
    /deep/ .el-upload-dragger{
        width: 280px;
        height: 210px;
    }
    .class{
        .el-autocomplete{
            width: 100%;
        }
    }
    .nextStep{
        margin-top: 20px;
        // position:absolute;
        // bottom:0;
        // left:0;
        .el-button{
            position: absolute;
            margin-top: 20px;
        }
        .nextBtn{
            right:0;
        }
    }
    @media screen and (max-width: 800px) {
        .el-range-editor.el-input__inner{
            width:100%;
        }
    }
}
</style>



